import React, { Component } from 'react'
import ProductContent from './components/admin/Content/product/ProductContent'
import UserContent from './components/admin/Content/user/UserContent'
import OrderContent from './components/admin/Content/order/OrderContent'
import Footer from './components/admin/Footer/Footer'
import Header from './components/admin/Header/Header'
import Menu from './components/admin/Menu/Menu'
import ProductTypeAdd from './components/admin/components/ProductTypeAdd'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import ProductControl from './components/admin/components/ProductContol'
import ProductTypeControl from './components/admin/components/ProductTypeControl'
import ProductTypeContent from './components/admin/Content/product/ProductTypeContent'
import Basket from './components/user/Component/BasketButton'
import NotFound from './pages/NotFound';
export default class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route path="/admin">
            <Header />
            <Menu />
          </Route>
        </Switch>
        <Switch>
          <Route path="/" exact>
            {/* <UserContent /> */}
            <Basket/>
          </Route>
          <Route path="/admin" exact>
            <OrderContent />
          </Route>
          <Route path="/admin/product" exact>
            <ProductContent />
          </Route>
          <Route path="/admin/product/type">
            <ProductTypeContent />
          </Route>
          <Route path="/admin/orders">
            <OrderContent />
          </Route>
          <Route path="/admin/users">
            <UserContent />
          </Route>
          <Route component={NotFound} />
        </Switch>
        <Switch>
          <Route path="/admin">
            <Footer />
          </Route>
        </Switch>
      </BrowserRouter>
    )
  }
}
