import React, { Component } from 'react'
import DataTable from '../../DataTable/products/ProductsDataTable'
import ProductControl from '../../components/ProductContol'
import StatusComponent from '../../components/StatusComponent/index'
import axios from './../../../../axios'

import { validation } from '../../../../util/Validator'
import firebase from "./../../../../firebase";
import tableName from './../../../../util/TableNames'
class ProductContent extends React.Component {
    defaultStatus = {
        productLoading: false,
        isUpdate: false,
        mainimage: '',
        product: {
            id: '',
            name: '',
            type: '',
            price: '',
            point: '',
            image: '',
            description: ''
        },
        errorMsg: {

            id: { show: false },
            name: { show: false },
            price: { show: false },
            point: { show: false },
            image: { show: false },
            description: { show: false },
            type: { show: false },
        }
    };
    tempProduct = {};
    constructor(props) {
        super(props);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.productAction = this.productAction.bind(this);
        this.setUpdateProduct = this.setUpdateProduct.bind(this);
        this.setAddProduct = this.setAddProduct.bind(this);
        this.state = {
            ...this.defaultStatus,
            productDatas: [],
            tableLoading: true,
            productTypes: [{
                id: '',
                name: '',
                image: '',
                description: '',
            }],
        }
    }
    productAdd(product) {
        axios.put("product/" + [product.id] + '.json', product).then(response => {
            alert("goy shaaj baina");
        });
        this.setState({
            product: {
                ...this.defaultStatus.product,
            },
            productLoading: false,
        });
    }


    handleInputChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        const type = target.type;
        const validationTypes = {
            price: 'POSITIVENUMBER',
            id: 'EQUAL'
        }
        if (type == 'file') {
            if (event.target.files[0]) {
                this.setState({ mainimage: event.target.files[0] });
            }
        }
        const validate = validation(value, validationTypes[name], name == 'id' && this.state.productDatas);

        let formValid = false;

        if (!validate.show) {
            let istemvavlid = true;
            Object.values(this.state.product).map((el, index) => {
                if (el.length == 0 && Object.keys(this.state.product)[index] != [name]) {
                    istemvavlid = false;
                    return;
                }
            })
            if (istemvavlid) {
                formValid = true;
            }
        }
        this.setState({
            errorMsg: {
                ...this.state.errorMsg,
                [name]: validate
            },
            product: {
                ...this.state.product,
                [name]: value
            },
            formValid: formValid
        });
    }

    productAction = () => {
        this.setState({ productLoading: true });
        if (!this.state.mainimage && this.state.isUpdate) {
            this.productAdd(this.state.product);
        }
        else {
            const uploadTask = firebase.storage().ref(`images/${this.state.product.id}`).put(this.state.mainimage);
            uploadTask.on(
                "state_changed",
                snapshot => {
                    const progress = Math.round(
                        (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                    );
                },
                error => {
                    console.log(error);
                },
                () => {
                    firebase.storage()
                        .ref("images")
                        .child(this.state.product.id)
                        .getDownloadURL()
                        .then(url => {
                            this.setState({
                                product: {
                                    ...this.state.product,
                                    image: url
                                }
                            })
                            this.productAdd(this.state.product);
                        });
                }
            );
        }
    };
    setUpdateProduct(updatedProduct, isUpdate) {
        this.tempProduct = updatedProduct;
        this.setState({
            ...this.defaultStatus,
            mainimage: null,
            formValid: true,
            isUpdate: isUpdate,
            product: updatedProduct,

        });
    }
    setAddProduct() {
        this.setState({ ...this.defaultStatus, formValid : false});
    }
    refreshProduct = () => {
        document.getElementById("exampleInputFile").value = '';
        this.setState({ 
            product: this.tempProduct,
            mainimage: ''
        });
    }

    getProductTypes() {
        axios.get('/productType.json').then(response => {
            if (response.data) {
                this.state.productTypes = Object.values(response.data).filter(function (el) {
                    return el != null;
                });;
                this.setState({
                    productTypes: this.state.productTypes
                });
            }
        });
    }
    getProductDatas() {
        const todoRef = firebase.database().ref('product');
        todoRef.on('value', (snapshot) => {
            this.setState({ tableLoading: true })
            if (snapshot.val() && snapshot.val() != null) {
                this.state.productDatas = Object.values(snapshot.val());
            }
            if (document.getElementById("datatableId"))
                document.getElementById("datatableId").remove()
            const script = document.createElement("script");
            this.setState({ tableLoading: false })
            script.setAttribute('id', "datatableId");
            script.src = "../js/content_datatable.js";
            script.async = true;
            document.body.appendChild(script);
            this.setState(this.state.productDatas);
        });
    }

    componentDidMount() {
        this.getProductTypes();
        this.getProductDatas();
    }

    render() {
        console.log('render product content')
        return (
            <div className="content-wrapper">
                <div className="content-header">
                    <div className="container-fluid">

                        <StatusComponent />
                        <div className="row mb-2">
                            <ProductControl refreshProduct = {this.refreshProduct} mainimage={this.state.mainimage} productAction={this.productAction} formValid={this.state.formValid} setProduct={this.setAddProduct} product={this.state.product} isUpdate={this.state.isUpdate} isLoading={this.state.productLoading} product={this.state.product} errorMsg={this.state.errorMsg} handleInputChange={this.handleInputChange} productTypes={this.state.productTypes}></ProductControl>
                        </div>
                    </div>
                </div>

                <section className="content">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-12">
                                <DataTable setProduct={this.setUpdateProduct} isLoading={this.state.tableLoading} productTypes={this.state.productTypes} productDatas={this.state.productDatas} />
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
export default ProductContent