import React from 'react';
import BasketItem from '../BasketItem'

import css from './style.module.css'
class Index extends React.Component {
    render() {
        return (
            <div>
                <div className={css.lamVFu}>
                    <div className={css.jKfBPg}>
                        <div className={css.gkbJjA}>
                            <div className={css.fhKndd}>
                                <i className="fa fa-shopping-basket"></i>
                                <span>4&nbsp;Items</span>
                            </div>
                            <button onClick={() => { this.props.buttonClick(!this.props.show) }} className={css.kWBUWu}>
                                <i className="fa fa-times"></i>
                            </button>
                        </div>
                        <div className={css.cartScrollbar}>
                            <div className={css.ospadding}>
                                <div className={css.iKtYkh}>
                                    <BasketItem key='1'></BasketItem>
                                    <BasketItem key='2'></BasketItem>
                                    <BasketItem key='3'></BasketItem>
                                    <BasketItem key='4'></BasketItem>
                                    <BasketItem key='5'></BasketItem>
                                    <BasketItem key='6'></BasketItem>
                                    <BasketItem key='7'></BasketItem>
                                </div>
                            </div>
                            {/* <div className={css.osscrollbarvertical}>
                                <div className={css.osscrollbartrackoff}>
                                    <div className={css.osscrollbarhandle}></div>
                                </div>
                            </div> */}
                        </div>
                        <div className={css.bhHvDf}>
                            <button className={css.dQOcYU}>
                                <a className={css.kVQlLW}>
                                    шалгах
                                </a>
                                <span className={css.iDSbKK}>{`$`}{`7.60`}</span>
                            </button>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}

export default Index;
