import React, { Component } from 'react'
import firebase from '../../../../firebase'
import axios from '../../../../axios'
import css from './style.module.css'
import tableName from './../../../../util/TableNames'
export default class ProductDataTable extends Component {

    getTypeText(typeId) {
        let typeText = '';
        this.props.productTypes.map(el => {
            if (typeId == el.id) {
                typeText = el.name;
            }
        })
        return typeText;
    }

    // tur ashiglahgui baigaa
    getData() {
        if (document.getElementById("datatableId")) {
            document.getElementById("datatableId").remove();
        }
        const script = document.createElement("script");

        axios.get('/orders.json').then(response => {
            this.props.productDatas = Object.values(response.data);
            this.setState(this.props.productDatas);
            this.setState({ isLoading: false })
            script.src = "js/content_datatable.js";
            script.async = true;
            document.body.appendChild(script);

        })
    }
    render() {
        console.log("product datatable ")
        return (
            this.props.isLoading == true ? <div className={css.loader}></div> : <div className="card">
                <div className="card-header">
                    <h3 className="card-title">Барааны жагсаалт</h3>
                </div>
                <div className="card-body" >
                    <table id="example1" className="table table-bordered table-striped">
                        <thead>
                            <tr>
                                <th></th>
                                <th>Дугаар</th>
                                <th>Нэр</th>
                                <th>Төрөл</th>
                                <th>Үнэ</th>
                                <th>Оноо</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.props.productDatas.map((el, index) => {
                                return (<tr key={el.id}>
                                    <td className={css.CardBodyImgTd}><img className={css.CardBodyImg} src={el.image || "http://via.placeholder.com/50"} alt="firebase-image" /></td>
                                    <td>{el.id}</td>
                                    <td>{el.name}</td>
                                    <td>{this.getTypeText(el.type)}</td>
                                    <td>{el.price}</td>
                                    <td>{el.point}</td>
                                    <td className={css.CardBodyEdit}><button id={css.CardIconCenter}
                                        data-toggle="modal" onClick={() => { this.props.setProduct(el, true, this.props.productDatas[index]) }} data-target="#modal-default" className="btn btn-success"><i className="fas fa-edit"></i></button>
                                    </td>
                                    <td className={css.CardBodyEdit}><button id={css.CardIconCenter} className="btn btn-danger"><i className="fas fa-trash-alt"></i></button></td>
                                </tr>)
                            })}
                        </tbody>

                        <tfoot>
                            <tr>
                                <th></th>
                                <th>Дугаар</th>
                                <th>Нэр</th>
                                <th>Төрөл</th>
                                <th>Үнэ</th>
                                <th>Оноо</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </tfoot>
                    </table>
                </div>
            </div>

        )
    }
}
