import React, { Component } from 'react'
import axios from "axios";
import css from './style.module.css'
import firebase from '../../../../../firebase'

export default class index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            data: [

            ]
        };
    }

    operations(btnId, iss){
        let ids = document.getElementById(btnId);
        console.log("sda yuma:", ids);
        if(iss){
            ids.style.display = 'block';
        }
        else{
            ids.style.display = 'none';
        }
    }

    getProductTypeDatas() {
        const todoRef = firebase.database().ref('productType');
        todoRef.on('value', (snapshot) => {
            this.setState({ loading: true })
            if (snapshot.val() && snapshot.val() != null) {
                this.state.data = Object.values(snapshot.val());
            }
            if (document.getElementById("datatableId"))
            document.getElementById("datatableId").remove()
            const script = document.createElement("script");
            this.setState({ loading: false })
            script.setAttribute('id', "datatableId");
            script.src = "../../js/content_datatable.js";
            script.async = true;
            document.body.appendChild(script);
            this.setState(this.state.data);
        });
    }

    componentDidMount() {
        this.getProductTypeDatas();
    }
    render() {
        let classNameHarah = [css.CardIconCenter, "btn btn-primary"]; 
        
        return (
            this.state.loading == true ? <div>Loading</div> :
            <div className="card">
                <div className="card-header">
                    <h3 className="card-title">Захиалгын жагсаалт</h3>
                </div>
                {/* /.card-header */}
                <div className="card-body">
                    <table id="example1" className="table table-bordered table-striped">
                        <thead>
                            <tr>
                                <th>Дугаар</th>
                                <th>Захиалагын код</th>
                                <th>Захиалагч</th>
                                <th>Утасны Дугаар</th>
                                <th>Огноо</th>
                                <th>Төлөв</th>
                                <th></th>
                            </tr>
                        </thead >
                        <tbody>
                            {this.state.data.map((el) => (
                                <tr >
                                    <td>{el.id}</td>
                                    <td>20210120115012</td>
                                    <td>980409</td>
                                    <td>99119911</td>
                                    <td>2021-01-16</td>
                                    <td>
                                    <div className={css.divBtnHide}>
                                        <select class = "form-control" onChange={()=>this.operations('hideBtn'+el.id, true)}>
                                        <option selected>Сонголтоо хий</option>
                                        <option value="1">Зөвшөөрөх</option>
                                        <option value="2">Захиалагдсан</option>
                                        <option value="3">Хүргэлт</option>
                                        <option value="3">Цуцлах</option>
                                        </select>  
                                        <button id={'hideBtn'+el.id} onClick={()=>this.operations('hideBtn'+el.id, false)} type="button" class={css.btnHidden}><i className='fa fa-check'></i></button>
                                    </div>
                                    </td>
                                    <td className={css.tdHarahCenter}>
                                    <button className={css.marginRight + " btn btn-success"} data-toggle="modal" data-target="#modal-default" ><i className="fas fa-search"></i>&nbsp;Харах</button>
                                    </td>
                                </tr>
                            ))}
                    </tbody>
                        <tfoot>
                            <tr>
                                <th>Дугаар</th>
                                <th>Захиалагын код</th>
                                <th>Захиалагч</th>
                                <th>Утасны Дугаар</th>
                                <th>Огноо</th>
                                <th>Төлөв</th>
                                <th></th>
                            </tr>
                        </tfoot>
                    </table>
                </div>
                {/* /.card-body */}
            </div>
        )
    }
}
