import axios from '../../axios';
import { stat } from 'fs';
import { validation } from '../../util/Validator'

const initialState = {
    completeAction: false,
    mainimage: null,
    product: {
        id: '',
        name: '',
        type: '',
        price: '',
        point: '',
        image: '',
        description: ''
    },
    errorMsg: {

        id: {},
        name: {},
        price: {},
        point: {},
        image: {},
        description: {},
        type: {},
    },

    formValid: false
}

const reducer = (state = initialState, action) => {
    console.log("action", action);
    if (action.type === 'PRODUCT_CHECK_VALIDATION') {
        const target = action.event.target;
        const value = target.value;
        const name = target.name;
        const type = target.type;
        if (type == 'file') {
            if (action.event.target.files[0]) {
                state.mainimage = action.event.target.files[0];
            }
        }
        const validate = validation(value, name == 'price' ? 'POSITIVENUMBER' : '');

        state.formValid = false;
        console.log("valid", validate)
        if (!validate.show) {
            let istemvavlid = true;
            Object.values(state.product).map((el, index) => {
                console.log("product", Object.values(state.product)[index])
                console.log(Object.keys(state.product)[index] != [name])
                if (Object.values(state.product)[index].length == 0 && Object.keys(state.product)[index] != [name]) {
                    istemvavlid = false;
                    return;
                }
            })
            if (istemvavlid) {
                state.formValid = true;
            }
        }

        return {
            ...state,
            formValid: state.formValid,
            mainimage: state.mainimage,
            errorMsg: {
                ...state.errorMsg,
                [name]: validate
            },
            product: {
                ...state.product,
                [name]: value
            }
        };
    }
    if (action.type === 'PRODUCT_ADD') {
        state.product.image = action.url;

        axios.post("/orders.json", state.product)
            .then(response => {
                alert("amjilttai hadgalagdlaa");
                return {
                    ...state,
                    product: {
                        completeAction: true
                    }

                }
            });
    }
    return state
}
export default reducer;