import React from 'react';
import css from './style.module.css'
import BasketDetail from '../BasketDetail'
class Index extends React.Component {
    constructor(props) {
        super(props);
        this.state = { show: false };
        this.buttonClick = this.buttonClick.bind(this);
    }
    buttonClick(show) {
        this.setState({ show: show });
    }
    render() {
        return (
            <div>
                <button className={css.jVFuuY} onClick={() => { this.buttonClick(!this.state.show) }}>
                    <span className={css.KDupa}><i className="fa fa-shopping-basket"></i>Item</span>
                    <span className={css.dCrbXJ}>$ 12345</span>
                </button>
                {/* <BasketDetail></BasketDetail> */}
                {/* <button className={css.marginRight + " btn btn-success"} data-toggle="modal" data-target="#modal-default" ><i className="fas fa-plus"></i> Нэмэх</button> */}
                {/* <div className="modal" id="modal-default" >
                    <div className="modal-dialog" style={{'position':'absolute','bottom':'0','margin':'0','width':'100%'}}>
                        <div className="modal-content">
                            <div className="modal-body"> */}
                {this.state.show && <BasketDetail buttonClick={this.buttonClick} show={this.state.show}></BasketDetail>}
                {/* </div>
                        </div>
                    </div>
                </div> */}
            </div>
        )
    }
}

export default Index;
