import React from 'react';
import css from './style.module.css'
class Index extends React.Component {

    render() {
        console.log("product add render")
        return (
            this.props.isLoading ? <h1>Loading</h1> : <div>
                <div className="form-group">
                    <label htmlFor="exampleInputId">Барааны код:</label>
                    <input disabled={this.props.isUpdate && 'disabled'} name='id' id='exampleInputId' value={this.props.product.id} type="number" onChange={(event) => { this.props.handleInputChange(event) }} className="form-control" placeholder="Барааны код" autoComplete="off"/>
                    <p className={css.errorMsg}>{this.props.errorMsg.id.show && this.props.errorMsg.id.message}</p>
                </div>
                <div className="form-group">
                    <label htmlFor="exampleInputPassword1">Барааны нэр:</label>
                    <input name='name' value={this.props.product.name} type="name" onChange={(event) => { this.props.handleInputChange(event) }} className="form-control" placeholder="Барааны нэр" autoComplete="off"/>
                    <p className={css.errorMsg}>{this.props.errorMsg.name && this.props.errorMsg.name.show && this.props.errorMsg.name.message}</p>

                </div>
                <div className="form-group">
                    <label>Барааны төрөл:</label>
                    <select name='type' value={this.props.product.type} onChange={(event) => { this.props.handleInputChange(event) }} className="form-control">
                        <option value=''></option>
                        {this.props.productTypes.map((el) => {
                            return (<option key={el.id} value={el.id}>{el.name}</option>)
                        })}
                    </select>
                    <p className={css.errorMsg}>{this.props.errorMsg.type.show && this.props.errorMsg.type.message}</p>
                </div>
                <div className="form-group">
                    <label>Барааны үнэ:</label>
                    <input type="number" value={this.props.product.price} min="0" name="price" className="form-control" onChange={(event) => { this.props.handleInputChange(event) }} placeholder="Барааны үнэ" autoComplete="off"/>
                    <p className={css.errorMsg}>{this.props.errorMsg.price.show && this.props.errorMsg.price.message}</p>
                </div>
                <div className="form-group">
                    <label>Тайлбар:</label>
                    <textarea value={this.props.product.description} className="form-control" name="description" rows="3" onChange={(event) => { this.props.handleInputChange(event) }} placeholder="Тайлбар оруулах ..."></textarea>
                    <p className={css.errorMsg}>{this.props.errorMsg.description.show && this.props.errorMsg.description.message}</p>
                </div>
                <div className="form-group">
                    <label>Оноо:</label>
                    <input name="point" value={this.props.product.point} type="number" min="0" className="form-control" rows="3" onChange={(event) => { this.props.handleInputChange(event) }} placeholder="Оноо оруулах ..." autoComplete="off"/>
                    <p className={css.errorMsg}>{this.props.errorMsg.point.show && this.props.errorMsg.point.message}</p>
                </div>
                <div className="form-group">
                    <label htmlFor="exampleInputFile">Барааны зураг:</label>
                    {this.props.isUpdate && this.props.isUpdate === true && !this.props.mainimage ?
                        <div className="input-group">
                            <img className={css.inputImage} src={this.props.product.image || "http://via.placeholder.com/20"} placeholder='Өмнөх зураг' alt='Өмнөх зураг' />
                        </div>
                        : ''
                    }
                    <div className="input-group">
                        <div className="custom-file">
                            <input type="file" name="image"  onChange={(event) => { this.props.handleInputChange(event) }} id="exampleInputFile" />
                            {/* <label className="custom-file-label" id='exampleInputFileLebel' htmlFor="exampleInputFile">Зураг сонгох...</label> */}
                        </div>

                    </div>
                    <p className={css.errorMsg}>{this.props.errorMsg.image.show && this.props.errorMsg.image.message}</p>
                </div>
            </div>
        )
    }
}

export default Index;
