import React, { Component } from 'react'

export default class index extends Component {
    render() {
        return (
            <div className="row">
                <div className="col-lg-3 col-6">
                    <div className="small-box bg-info">
                        <div className="inner">
                            <h3>150</h3>
                            <p>Батлагдаагүй захиалгын хүсэлт</p>
                        </div>
                        <div className="icon">
                            <i className="ion ion-bag" />
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-6">
                    <div className="small-box bg-success">
                        <div className="inner">
                            <h3>200</h3>
                            <p>Нийт захиалга</p>
                        </div>
                        <div className="icon">
                            <i className="ion ion-stats-bars" />
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-6">
                    <div className="small-box bg-warning">
                        <div className="inner">
                            <h3>44</h3>
                            <p>Хэрэглэгч бүртгүүлэх хүсэлт</p>
                        </div>
                        <div className="icon">
                            <i className="ion ion-person-add" />
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-6">
                    <div className="small-box bg-danger">
                        <div className="inner">
                            <h3>15</h3>
                            <p>Нийт бүртгэдсэн бараа</p>
                        </div>
                        <div className="icon">
                            <i className="ion ion-pie-graph" />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
