import React from 'react'
import css from './style.module.css'
import ProductAdd from '../ProductAdd'

export default class ProductControl extends React.Component {
    // componentDidMount() {
    //     const script = document.createElement("script");
    //     script.src = "js/successToast.js";
    //     script.async = true;
    //     document.body.appendChild(script);
    //     console.log("product control render didmount");
    // }
    productFormClose() {
        document.getElementById("exampleInputFile").value = '';
    }
    render() {
        console.log("product control render");
        return (
            <div className="col-sm-12 text-right">
                <button onClick={() => {
                    this.props.setProduct();
                }} className={css.marginRight + " btn btn-success"} data-toggle="modal" data-target="#modal-default" ><i className="fas fa-plus"></i> Нэмэх</button>

                <div className="modal fade" id="modal-default" data-backdrop="static" data-keyboard="false">
                    <div className="modal-dialog">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h4 className="modal-title">Бараа нэмэх</h4>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => { this.productFormClose() }}>
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            {this.props.isLoading == true ? <div className={css.loader}></div> : <div className="modal-body">
                                <div className="card card-primary" >
                                    <div className="card-body" style={{ "textAlign": "left" }}>
                                        <ProductAdd mainimage={this.props.mainimage} productAction={this.props.productAction} isLoading={this.props.isLoading} product={this.props.product} errorMsg={this.props.errorMsg} handleInputChange={this.props.handleInputChange} isUpdate={this.props.isUpdate} productTypes={this.props.productTypes}></ProductAdd>
                                        {this.props.isUpdate == true ? <div className="modal-footer justify-content-between">
                                            <button type="button" className="btn btn-danger" data-dismiss="modal">Хаах</button>

                                            <button type="submit" onClick={this.props.productAction} disabled={this.props.formValid ? "" : "disabled"} className="btn btn-success">Засах</button>
                                            <button type="button" onClick={this.props.refreshProduct} className="btn btn-info"><i className="fas fa-sync-alt"></i></button>
                                        </div>
                                            : <div className="modal-footer justify-content-between">
                                                <button type="button" className="btn btn-danger" onClick={() => { this.productFormClose() }} data-dismiss="modal">Хаах</button>
                                                <button type="submit" onClick={this.props.productAction} disabled={this.props.formValid ? "" : "disabled"} className="btn btn-success">Хадгалах</button>
                                            </div>}
                                    </div>
                                </div>
                            </div>
                            }
                        </div>
                    </div>
                </div>

            </div >
        )
    }
}
