import React from 'react';
import { validation } from '../../../../util/Validator'
import css from './style.module.css'
import storage from "./../../../../firebase";
class Index extends React.Component {

    render() {
        console.log("product add render", this.props);
        return (
            this.props.isLoading ? <h1>Loading</h1> : <div>
            <div className="form-group">
                <label htmlFor="exampleInputId">Төрөлийн дугаар:</label>
                <input autoComplete="off" name='id' disabled={this.props.isUpdate && 'disabled'}  value={this.props.productType.id}  type="number" onChange={(event) => { this.props.handleInputChange(event) }} className="form-control" id="exampleInputId" placeholder="Төрөлийн дугаар" />
                <p className={css.errorMsg}>{this.props.errorMsg.id.show && this.props.errorMsg.id.message}</p>
            </div>
            <div className="form-group">
                <label htmlFor="exampleInputPassword1">Төрөлийн нэр:</label>
                <input autoComplete="off" name='name' value={this.props.productType.name} type="text" onChange={(event) => { this.props.handleInputChange(event) }} className="form-control" placeholder="Төрөлийн нэр" />
                <p className={css.errorMsg}>{this.props.errorMsg.name.show && this.props.errorMsg.name.message}</p>

            </div>
            <div className="form-group">
                <label>Тайлбар:</label>
                <textarea className="form-control" value={this.props.productType.description
                } name="description" rows="3" onChange={(event) => { this.props.handleInputChange(event) }} placeholder="Тайлбар оруулах ..."></textarea>
                <p className={css.errorMsg}>{this.props.errorMsg.description.show && this.props.errorMsg.description.message}</p>
            </div>
            <div className="form-group">
            <label htmlFor="exampleInputFile">Tөрөлийн зураг:</label>
            { this.props.isUpdate && this.props.isUpdate === true && !this.props.mainimage ? 
                <div className="input-group">
                        <img className={css.inputImage} src={this.props.productType.image || "http://via.placeholder.com/20"} placeholder='Өмнөх зураг' alt='Өмнөх зураг' />
                </div>
                : ''
            }
            <div className="input-group">
                <div className="custom-file">
                    <input type="file" name="image" onChange={(event) => { this.props.handleInputChange(event) }} id = "exampleInputFile"/>
                    {/*<label className="custom-file-label" id='exampleInputFileLebel' htmlFor="exampleInputFile">Зураг сонгох...</label>*/}
                </div>
                
            </div>
            <p className={css.errorMsg}>{this.props.errorMsg.image.show && this.props.errorMsg.image.message}</p>
            </div>
        </div>
        )
    }
}

export default Index;
